<template>
  <div id="app">
    <div class="container">
      <h1>國立員林家商108學年度第1次代理教師甄選</h1>
      <p>
        *各科配分比例如簡章－
        英文科、國際貿易科及資訊科技概論科：試教60%、口試40%
        商業經營科：試教50%、口試50%
        美容科、：實作80%、口試20%
        體育科：試教70%、口試30%
      </p>
      <div>
        <label for="search-element">請輸入身分證字號（首字大寫英文）</label>
        <form class="form-inline">
          <div class="form-group">
            <div class="input-group">
              <input
                v-model="searchKey"
                type="text"
                class="form-control"
                id="exampleInputAmount"
                placeholder="ex.E123916382"
              />
            </div>
            <!--     <button type="submit" class="btn btn-primary">確認</button> -->
          </div>
        </form>

        <div class="filters row">
          <table class="table">
            <thead>
              <tr>
                <th>准考證號碼</th>
                <th>姓名</th>
                <th>身分證字號</th>
                <th></th>
                <th>筆試原始分</th>
                <th>試教原始分</th>
                <th>口試原始分</th>
                <th>實作原始分</th>
                <th>加權後總分</th>
                <th>正取最低分</th>
                <th>結果</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="product in filteredProducts">
                <!-- tr v-for="product in products" -->
                <!-- tr v-for="product in products | filterBy searchKey in 'name'" -->
                <td>{{ product.num }}</td>
                <td>{{ product.name }}</td>
                <td>{{ product.id }}</td>
                <td>{{ product.score1 }}</td>
                <td>{{ product.firstResult }}</td>
                <td>{{ product.score2 }}</td>
                <td>{{ product.score3 }}</td>
                <td>{{ product.score4 }}</td>
                <td>{{ product.total }}</td>
                <td>{{ product.lowScore }}</td>
                <td>{{ product.finalResult }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
var products = [];

function findProduct(productId) {
  return products[findProductKey(productId)];
}

function findProductKey(productId) {
  for (var key = 0; key < products.length; key++) {
    if (products[key].id == productId) {
      return key;
    }
  }
}

export default {
  name: "app",
  data() {
    return {
      products: products,
      searchKey: ""
    };
  },
  computed: {
    filteredProducts() {
      return this.products.filter(product => {
        return product.id == this.searchKey;
      });
    }
  },
  created() {
    let self = this;
    axios.get("https://test-d1725.firebaseio.com/user.json").then(response => {
      // self.tenOne = response.data[0];
      self.products = response.data;
      // console.log(response.data);
    });
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



